.lobby-style{
  padding: 1rem;
}

.lobby-style h1{
  font-size: 84px;
  color: #804242;
}

.lobby-style ul{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  list-style-type: none;
}

.header{
  display: flex;
  justify-content: space-between;
}

.join-form{
  display: flex;
  justify-content: center;
  align-items: center;
}

.join-form-input input{
  border: none;
  width: 640px;
  height: 60px;
  border-radius: 15px;
  padding-left: 2rem;
  font-size: 15px;
}

.input-room-button{
  display:flex;
  justify-content:center;
  align-items:center;
  width: 100px;
  height: 50px;
  background-color: #BE6969;
  margin-left: 1rem;
  border-radius: 15px
}

.input-room-button a{
  text-decoration: none;
  color: white
}
