.gamecard{
  display: block;
  text-align: center;
  margin: 1rem 0;
  background-color: white;
  height: 100%;
  width: 80%;
  border-radius: 25px;
}
.gamecard-container{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: grey;
  color: white;
  border-radius: 25px;
  width: 100%;
  height: 100px;
  margin-bottom: 2rem;
  position: relative
}

.gamename{
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  color: #804242;
  width: 100%;
  bottom: -1.25rem;
}

.text{
  background-color: #E1D7D7;
  border-radius: 18px;
  height: 46px;
  padding: 1rem;
  left: 0;
  width: 100;
}


